import React from 'react';
import s from './Login.module.scss';
import {Field, reduxForm} from 'redux-form';
import {setDataLoginTC} from '../../../redux/loginReducer';
import * as axios from 'axios';
import {setLogin} from '../../../redux/loginReducer';
const LoginForm = (props)=>{
    return(
        <form onSubmit={props.handleSubmit}>
            <Field component={"input"} name={"login"} className={s.input} placeholder="login" type="text"/>
            <Field component={"input"} name={"password"} placeholder="password" type="text"/>
            <Field component={"input"} name={"rememberMe"} type="checkbox"/>
            <button className={s.btn}>Login</button>
        </form>
    )
}
const LoginReduxForm = reduxForm({form: 'login'})(LoginForm);
const Login = (props)=>{

    let onSubmit = (formData)=>{
        let email = formData.login;
        let password = formData.password;
        setDataLoginTC(email, password);
        debugger
    }

    let bla = (formData)=>{
        let email = formData.login;
        let password = formData.password;
        debugger
        axios.post("https://reqres.in/api/login",{email: email, password: password})
        .then((response)=>{
            setLogin(response.data.token)
        })
    }
    return(
        <div className={s.login_page}>
            <LoginReduxForm onSubmit={bla} />
            <button onClick={bla}>sdsd</button>
        </div>
        
    )
}
export default Login;