import * as axios from 'axios';
let instance = axios.create({
    baseURL: "https://reqres.in/api/"
});
export let UsersAPI = ()=>{
    return  instance.get(`users?page=1`)
        .then((response)=>{
            return response.data
        })
    
};
export let giveNewPageAPI = (p)=>{
  return  instance.get(`users?page=${p}`)
  .then((response)=>{
      return response.data
  })
}
export let ProfileContainerAPI = (userId)=>{
    return instance.get("users/"+userId)
    .then((response=>{
        return response.data
    }))
}

export let loginAPI = (email, password)=>{
    debugger
    return instance.post("login",{email: email, password: password})
        .then((response)=>{
            return response;
        })
}