const SEND_MESSAGE = "SEND-MESSAGE";

let initialState = {
    dataName: [
        {id: 1,name: "Dmitriy"},
        {id: 2,name: "Ira"},
        {id:3, name: "Vasya"},
        {id: 4,name: "Kolya"},
        {id: 5, name: "Galya"} 
    ],
    dataMessage: [
        {id: 1, message: "Hello"},
        {id: 2, message: "I'm Robbi"},
        {id: 3, message: "And you?"}
    ]
}
const dialogsReducer = (state = initialState, action)=>{
    switch (action.type) {
        case SEND_MESSAGE:
            let stateCopy = {...state};
            stateCopy.dataMessage = [...state.dataMessage];
            stateCopy.dataMessage.push({
                id:4,
                message: action.text
            });
            return stateCopy;
        default:
            return state;
    }
}
export const callSendMessage = (text)=>{
    return{
        type:SEND_MESSAGE,
        text
    }
}
export default dialogsReducer;