import {loginAPI} from '../api/axios';
const LOGIN = "LOGIN";

let initialState = {
    data: ''
};
const loginReducer = (state= initialState, action)=>{
    switch (action.type){
        case LOGIN:{
            return{
                ...state,
                data: action.responses
            }
        }
        default:{
            return state;
        }
    }
} 
export const setLogin = (responses)=>{
    debugger
    return{
        type: LOGIN,
        responses
    }
}
export const setDataLoginTC = (email, password)=>{
    debugger
    return (dispatch)=>{
        loginAPI(email, password).then((response)=>{
            dispatch(setLogin(response))
        });
    }

};


export default loginReducer;