import {ProfileContainerAPI} from '../api/axios';
const SEND = "SEND";
const IS_CHANGE = "IS-CHANGE";
const SET_DATA ="SET_DATA";

let initialState = {
    data: {
        id: 2,
        email: "janet.weaver@reqres.in",
        first_name: "Janet",
        last_name: "Weaver",
        avatar: "https://s3.amazonaws.com/uifaces/faces/twitter/josephstein/128.jpg"
        },
    newText: "",
    messages: [
        {
        avatar: "https://s3.amazonaws.com/uifaces/faces/twitter/josephstein/128.jpg",
        first_name: "Katya",
        message: "hello"
    }
    ]
}
const profileReducer = (state = initialState, action)=>{
    switch (action.type) {
        case SEND:{
            let stateCopy = {...state};
            stateCopy.messages = [...state.messages];
            stateCopy.messages.push({
                id:4, avatar: "https://s3.amazonaws.com/uifaces/faces/twitter/josephstein/128.jpg", first_name:"Karl", message: stateCopy.newText
            });
            stateCopy.newText = '';
            return stateCopy;
        }
        case IS_CHANGE:{
            let stateCopy = {...state};
            stateCopy.newText = action.text; 
            return stateCopy;
        }
        case SET_DATA:{
            return{
                ...state,
                data: action.data
            }
        }
        default:
            return state;
    }
}
export const callSend =() =>{
    return {
        type: SEND
    }
};
export const callOnChange = (znaks)=>{
    return{
        type: IS_CHANGE,
        text: znaks
    }
}
export const setData = (data)=>{
    return{
        type: SET_DATA,
        data
    }
}

export const setDataUserProfile = (userId)=>{
    return (dispatch)=>{
        ProfileContainerAPI(userId).then((data)=>{
            dispatch(setData(data.data));
            debugger
        })
    }
}
export default profileReducer;
