import React from 'react';
import s from './FriendsIco.module.scss';
const FriendsIco = (props)=>{
    return(
        <div className={s.icon_block}>
               <div className={s.img}> <img></img></div>
                <div className={s.name}>{props.name}</div>
        </div>
    )
}
export default FriendsIco;