import React from 'react';
import s from './Posts.module.scss';
import Post from './Post/Post';
const Posts = (props)=>{
   
    let post = props.data.map(e=> <Post avatar={e.avatar} first_name={e.first_name} message={e.message} />);
    let znak = React.createRef();
    let send =(e)=>{
        e.preventDefault() ;
        props.callSend()
    }
    let change= ()=>{
        let znaks = znak.current.value;
        props.callOnChange(znaks);
    }
    return(
        <div className={s.posts}>
            <div className={s.title}>My post</div>
            <form>
                <textarea ref={znak} onChange={change} value={props.newText} name="post"></textarea><br></br>
                <button onClick={send} className={s.btn}>Send</button>
            </form>
            {post}
        </div>
    )
}
export default Posts;