import React from 'react';
import s from './Description.module.scss';

const Description = (props)=>{
    return(
        <div className={s.descr}>
            <div className={s.name}>{props.first_name} {props.last_name}</div>
            <div className={s.item}>Date of Birth: 20.05.1993</div>
            <div className={s.item}>Education: LNU name of I.Franko</div>
            <div className={s.item}>Email:  {props.email} </div>
        </div>
    )
}
export default Description;