import React from 'react';
import s from './Dialogs.module.scss';
import {NavLink} from 'react-router-dom';
import {Field, reduxForm} from 'redux-form';

const Names = (props)=>{
    let ids = "/dialogs/" + props.id;
    return( <div>
        <span className={s.dot}></span>
        <NavLink activeClassName={s.active} to={ids}> {props.name} </NavLink>
        </div>
    )
}
const Message = (props)=>{
    return(
        <div className={s.message_block}>
            <div className={s.smile}><img src="/img/smile.png"></img></div>
            <div className={s.message_item}>{props.message}</div>
        </div>
    )
};

const Dialogs = (props)=>{
   
let namesMap = props.dataName.map((e)=> <Names id={e.id} name={e.name}/>);
let messageMap = props.dataMessage.map(e => <Message id={e.id} message={e.message}/>)

let onSubmit = (value)=>{
    props.giveMessage(value.text);
}
    return(
        <div className={s.dialogs}>
            <div className={s.title} >DIALOGS</div>
            <div className={s.block}>
                <div className={s.names}>
                    {namesMap}
                </div>
                <div className={s.line}></div>
                <div className={s.message}>
                   {messageMap}
                </div>
            </div>
            
            <DialogsFormRedux onSubmit={onSubmit} />
        </div>
    )

}

const DialogsForm = (props)=>{
    return(
        <form className={s.send_message} onSubmit={props.handleSubmit}>
            <Field name={"text"} component={"input"} className={s.textarea}></Field>
            <button   className={s.btn}>Send message</button>
        </form>
    )
}
const DialogsFormRedux = reduxForm({form: "dialogs"})(DialogsForm);
export default Dialogs;