import React from 'react';
import s from './Post.module.scss';

const Post = (props)=>{
    return(
        <div className={s.post}>
            <img src={props.avatar} alt="foto" />
            <div className={s.name}>{props.first_name}</div>
            <div className={s.descr}>{props.message} <hr></hr></div>
            
        </div>
    )
}
export default Post;