import React from 'react';
import Profile from './Profile';
import { connect } from 'react-redux';
import * as axios from 'axios';
import {setData,callOnChange,callSend, setDataUserProfile} from '../../../redux/profileReducer';
import {withRouter} from 'react-router-dom';
import {ProfileContainerAPI} from '../../../api/axios';
class ProfileContainer extends React.Component{
    
    constructor(props){
        super(props);
        let userId = this.props.match.params.userId;
        this.props.setDataUserProfile(userId)
    }
    render(){
        return(
            <Profile state={this.state} data={this.props.data}/>
        )
    }
}
const mapStateToProps = (state)=>{
    return{
        state: state.profile,
        data: state.profile.data
    }
}
let WithUrlContainer =  withRouter(ProfileContainer);
export default connect(mapStateToProps,{setData,callOnChange,callSend, setDataUserProfile}) (WithUrlContainer);