import React from 'react';
import s from './Users.module.scss';
import * as axios from 'axios';
import UserItem from './UserItem';
import {UsersAPI, giveNewPageAPI} from '../../../api/axios.js';

class UsersC extends React.Component{
    componentDidMount(){
        
        UsersAPI().then( data=> {
            this.props.setUsers(data.data)
            this.props.setTotal(data.total)
         } )
    }
    giveNewPage = (p)=>{
        
        giveNewPageAPI(p).then((data)=>{
            this.props.setUsers(data.data)
            this.props.setPage(p)
        })
    }
    render(){
        let pagesCount = Math.ceil(this.props.total / this.props.per_page);
        let pages = [];
        for(var i =1; i<= pagesCount;i++){
            pages.push(i);
        }
        return(
            <div className={s.users}>
                <div className={s.title}>Users</div>
                {pages.map((p)=>{
                   return <span onClick={()=> this.giveNewPage(p)} className={`${s.page} ${this.props.page === p && s.page_active}`}>{p}</span>
                })}
            {this.props.users.map((e)=>{
                return(
                    <UserItem  follow={this.props.follow} unfollow={this.props.unfollow} followed={e.followed} avatar={e.avatar} id={e.id} userId={e.userId} email={e.email} last_name={e.last_name} first_name={e.first_name}/>
                )
            })}
            </div>
        )
    }
}
export default UsersC;