import React from 'react';
import s from './ProfileStatus.module.scss';
class ProfileStatus extends React.Component{
    state = {
        editMode: false,
        status: "status"
    }
    changeToInput = ()=>{
        this.setState({
            editMode: true
        })
    }  
    changeToSpan = ()=>{
        this.setState({
            editMode: false
        })
    }
    changeValue =(e)=>{
        this.setState({
            status: e.target.value
        })
    }
  render(){
        return(
            <div>
                {!this.state.editMode ? 
                <div className={s.status}>
                    <span onDoubleClick={this.changeToInput}>{this.state.status}</span>
                </div> :
                <div className={s.input}> <input onChange={(e)=> this.changeValue(e)} value={this.state.status} autoFocus={true} onBlur={this.changeToSpan}></input> </div>}
            </div>
        )
    }
}

export default ProfileStatus;