import React from 'react';
import s from './Profile.module.scss';
import UpImg from './UpImg/UpImg';
import Description from './Desription/Description';
import PostsContainer from './Posts/PostsContainer';
import ProfileStatus from './ProfileStatus';
import dog from '../../../img/dog.jpg';
const Profile = (props)=>{
    return(
        <div className={s.profile}>
            <UpImg/>
            <div className={s.profile_block}>
                <div className={s.foto}><img src={props.data.avatar}/></div>
                <ProfileStatus/>
                <Description email={props.data.email} first_name={props.data.first_name} last_name={props.data.last_name}/>
            </div>
            <PostsContainer />
        </div>
    )
}
export default Profile;