import React from 'react';
import s from './Menu.module.scss';
import FriendsIco from './FriendsIco/FriendsIco';
import {NavLink} from 'react-router-dom';
function Menu(props){
return (
    <div className={s.menu}>
        <NavLink activeClassName={s.active} to="/profile">Profile</NavLink>
        <NavLink activeClassName={s.active} to="/dialogs">Messages</NavLink>
        <NavLink activeClassName={s.active} to="/music">Music</NavLink>
        <NavLink activeClassName={s.active} to="/settings">Settings</NavLink>
        <NavLink activeClassName={s.active} to="/users">Find friends</NavLink>
        <NavLink activeClassName={s.active} className={s.friends} to="/friends">Friends</NavLink>
        <div className={s.friends_icons}>
            <FriendsIco name={props.stateName[0].name}/>
            <FriendsIco name={props.stateName[1].name}/>
            <FriendsIco name={props.stateName[2].name}/>
        </div>
    </div>
)
};
export default Menu;
