import React from 'react';
import s from './Users.module.scss';
import {NavLink} from 'react-router-dom';

const UserItem = (props)=>{
    let follow=()=>{
      props.follow(props.id)
    }
   let unfollow=()=>{
      props.unfollow(props.id)
    }
    let link = `profile/${props.id}`;
        return(
            <div key={props.id} className={s.user_item}>
            <div className={s.img_box}> <NavLink to={link}><img src={props.avatar}></img></NavLink> </div>
            <div>{props.followed ? <button  onClick={unfollow} className={s.follow}>
                unfollow </button> : <button onClick={follow} className={s.follow}>
                follow </button>}</div>
            <div className={s.descr_block}>
                <div className={s.left_block}>
                    <div className={s.name}>{props.first_name} {props.last_name}</div>
                    <div className={s.about_me}>{props.email}</div>
                </div>
                <div className={s.right_block}>
                    <div className={s.location}>Ukraine</div>
                    <div className={s.location}>Lviv</div>
                </div>

            </div>
        </div>
        )
    
}

export default UserItem;